<template>
    <div>
        <Head title="个人中心"></Head>
        <div class="person"  v-if="token">
            <!-- <h2>个人中心 </h2> -->
            <!-- <h4>token == {{token}} </h4> -->
            <h2>用户名 &nbsp;&nbsp;  {{userInfo.username}} </h2>
            <h2>手机号  {{userInfo.mobile}}</h2>
            <UploadImg class="imgsize"  ref="one"></UploadImg>   
        </div>
        <div v-else>
            <h4>亲，你还没有登录哦，请马上去登录</h4>
        </div>
    </div>
</template>

<script>
import{
    myMixin
}from "@/utils"
import{
    baseURL
} from "@/utils/axios"
export default {
    mixins:[myMixin],  //混入到组件实例
    data(){
        return{
            token:sessionStorage.token||"",
            userInfo:{},
            count:1314
        }
    },
    created(){
        console.log("1903-daydayup")
    },
    mounted(){
        this.$axios.get("/vue/getuserinfo")
        .then(res=>{
            if(res.data.type){
                this.userInfo = res.data.result;
                if(res.data.result.avatar){
                    //this.$refs.one.pic = res.data.result.avatar.replace(/public/,baseURL);
                }
            }
        })
    }
}

</script>


<style lang="scss" scoped>
h2{
    padding:10px;
    margin-top:20px;
    color:black;
    font-size:20px;
}
.person{
    position: relative;
}
.imgsize{
    position: absolute;
    top:5px;
    right:10px;
}

</style>